import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';


import { AppComponent } from './app.component';

import { IndexComponent } from './componentes/index';
import { FooterComponent } from './componentes/footer.component'
import { FrasesComponent } from './componentes/frases.component'
import { PanelNoticiasComponent } from './componentes/panelnoticias.component'
import { HeaderComponent } from './componentes/header.component'
import { EntradaIndexComponent } from './componentes/entradaindex.component'
import { NosotrosComponent } from './componentes/nosotros'
import { MisionVisionComponent } from './componentes/misionvision.component'
import { InicialPrimariaComponent } from './componentes/inicialprimaria.component'
import { NivelesComponent } from './componentes/niveles'


import { routing, appRoutingProviders } from './app.rutas';

@NgModule({
  declarations: [
    AppComponent,
    IndexComponent,
    FooterComponent,
    FrasesComponent,
    PanelNoticiasComponent,
    HeaderComponent,
    EntradaIndexComponent,
    NosotrosComponent,
    MisionVisionComponent,
    InicialPrimariaComponent,
    NivelesComponent
  ],
  imports: [
    BrowserModule,
    routing
  ],
  providers: [appRoutingProviders],
  bootstrap: [AppComponent]
})
export class AppModule { }



